import React from "react";
import { Dialog, DialogContent, DialogTitle, Typography, TextField, Button, CircularProgress, DialogActions, Divider } from "@material-ui/core";
import ConfirmOrder from "../Order/ConfirmOrder";
import API from "../../Services/API";
import { DocToString } from "../../Services/Calcs";

export default class OrderDetails extends React.Component<{ onClose: (refresh: boolean) => void, match: any, editable: boolean }> {
    state = {
        loading: "data",
        _fechaEntrega: ""
    } as VisibleOrder
    componentWillMount() {
        API.GetOrder(+this.props.match.params.id)
            .then(order => this.setState({ ...order, loading: null }));
    }
    handleClose = (event: React.MouseEvent<HTMLElement>) => {
        this.props.onClose(this.state.shouldRefresh);
    }
    handleButton = (event: React.MouseEvent<HTMLButtonElement>) => {
        const { name } = event.currentTarget;
        if (name === "APROBAR") {
            if (!this.state._fechaEntrega)
                return alert("Agregue la fecha de Entrega");
            API.ActionOrder(this.state.id, { action: name, value: this.state._fechaEntrega })
                .then(x => this.setState({
                    estado: "APROBADO",
                    fechaEntrega: this.state._fechaEntrega,
                    loading: null,
                    shouldRefresh: true
                })).catch(ex => {
                    alert(ex.message);
                    this.setState({ loading: null });
                });
        }
        if (name === "RECHAZAR") {
            let cf = window.confirm("Esta seguro de rechazar este pedido?");
            if (!cf) return;
            let value = window.prompt("Digita el Motivo de Rechazo");
            if (!value) return;
            API.ActionOrder(this.state.id, { action: name, value })
                .then(x => this.setState({ estado: "RECHAZADO", loading: null, shouldRefresh: true }))
                .catch(ex => {
                    alert(ex.message);
                    this.setState({ loading: null });
                });
        }
        if (name === "ENTREGAR") {
            let cf = window.confirm("Esta seguro de seleccionar este pedido como ENTREGADO?");
            if (!cf) return;
            API.ActionOrder(this.state.id, { action: name, value: null })
                .then(x => this.setState({ estado: "ENTREGADO", loading: null, shouldRefresh: true }))
                .catch(ex => { alert(ex.message); this.setState({ loading: null }) });
        }
        this.setState({ loading: name });
    }
    handleChange = (event: React.ChangeEvent<HTMLInputElement>) => {
        const { name, value } = event.currentTarget;
        this.setState({ [name]: value })
    }
    render() {
        const { numero,
            fechaCreacion, fechaEntrega, _fechaEntrega, detalle,
            descuentoPorcentaje, descuentoTotal, position, observacion,
            direccion, sector, ciudad, referencia, numeroTransaccion,
            estado, total, username,
            survey1, survey2, loading,
            documento, identificacion, razonSocial,
            telefono_factura, direccion_factura, email_factura
        } = this.state;
        const editable = this.props.editable === true ? true : null;

        if (loading === "data")
            return (<Dialog open={true} onClose={this.handleClose} maxWidth="md" >
                <DialogContent style={{ textAlign: "center", paddingTop: 100, width: 400, height: 400 }}>
                    <CircularProgress size={100} />
                    <Typography variant="h6">CARGANDO...</Typography>
                </DialogContent>
            </Dialog>);


        return (<Dialog open={true} onClose={this.handleClose} maxWidth="md">
            <DialogTitle>PEDIDO #{numero}</DialogTitle>
            <DialogContent>
                <TextRow value={new Date(fechaCreacion).toLocaleString()} label="Fecha" />
                <TextRow value={estado} label="Estado" />
                <TextRow value={username} label="Usuario" />

                <SectionHeader>PRODUCTOS</SectionHeader>
                <ConfirmOrder items={detalle} descuento={{ porcentaje: descuentoPorcentaje, valor: descuentoTotal }} total={total} subtotal={total + descuentoTotal} />
                <TextRow value={observacion} label="Observacin" />

                <SectionHeader>DATOS DE ENTREGA</SectionHeader>
                <TextRow value={ciudad} label="Ciudad" />
                <TextRow value={sector} label="Sector" />
                <TextRow value={direccion} label="Direccion" />
                <TextRow value={referencia} label="Referencia" />

                <SectionHeader>DATOS DE FACTURACIN</SectionHeader>
                <TextRow value={identificacion} label={DocToString(documento as any)} />
                <TextRow value={razonSocial} label="Razon Social" />
                <TextRow value={direccion_factura} label="Direccin" />
                <TextRow value={telefono_factura} label="Telfono" />
                <TextRow value={email_factura} label="Email" />

                <SectionHeader>PAGO</SectionHeader>
                <TextRow value={numeroTransaccion} label="Comprobante" />

                {position && <Button
                    variant="contained"
                    fullWidth
                    style={{ marginTop: 10, marginBottom: 10 }}
                    color="primary"
                    component="a"
                    href={`https://www.google.com/maps/search/?api=1&query=${position.lat},${position.lng}`}
                    target="_blank"
                >VER UBICACION DE ENTREGA</Button>}

                {editable && estado === "PENDIENTE"
                    ? (<div style={{ display: "flex", flexDirection: "row", marginTop: 15, marginBottom: 10 }}>
                        <Typography variant="button" style={{ marginRight: 10, paddingTop: 5 }} >Fecha de Entrega</Typography>
                        <TextField
                            type="date"
                            fullWidth
                            name="_fechaEntrega"
                            value={_fechaEntrega || ""}
                            style={{ flex: 1 }}
                            onChange={this.handleChange}
                        /></div>)
                    : null}

                {(editable && estado === "PENDIENTE")
                    ? (<div style={{ marginTop: 10, marginBottom: 10, display: "flex", flexDirection: "row" }}>
                        <Button onClick={this.handleButton} name="RECHAZAR"
                            style={{ marginLeft: 10, marginRight: 10, flex: 1 }}
                            variant="text" color="secondary"
                            disabled={Boolean(loading)}
                        >{loading === "RECHAZAR" ? <CircularProgress size={25} color="inherit" /> : "RECHAZAR"}</Button>
                        <Button onClick={this.handleButton} name="APROBAR"
                            style={{ marginLeft: 10, marginRight: 10, flex: 1 }}
                            variant="outlined" color="primary"
                            disabled={Boolean(loading)}
                        >{loading === "APROBAR" ? <CircularProgress color="inherit" size={25} /> : "APROBAR"}</Button>
                    </div>)
                    : null}

                <Divider style={{ margin: 10 }} />
                <Typography style={{ marginTop: 10 }} variant="caption">Cmo podemos ayudarte a mejorar tus ventas?</Typography>
                <Typography>{survey1 || ""}</Typography>
                <Typography style={{ marginTop: 10 }} variant="caption">
                    Comprtenos alguna experiencia que hayan tenido tus clientes usando nuestros productos
                </Typography>
                <Typography>{survey2 || ""}</Typography>
                
            </DialogContent>
            <DialogActions>
                {editable && estado === "APROBADO" ? (<Button color="primary" name="ENTREGAR" onClick={this.handleButton}>
                    {loading === "ENTREGAR" ? <CircularProgress color="inherit" size={25} /> : "ENTREGAR"}
                </Button>) : null}
                <Button onClick={this.handleClose}>SALIR</Button>
            </DialogActions>
        </Dialog>);
    }
}

interface VisibleOrder extends Order {
    loading: string;
    _fechaEntrega: string;
    shouldRefresh: boolean;
}

function TextRow(props: { value: any, label: any }) {
    const { value, label } = props;
    return <div style={{ width: "100%", textAlign: "left" }}>
        <div style={{ width: 120, display: "inline-block" }}>
            <Typography variant="body1" >{label}</Typography>
        </div>
        <Typography variant="body2" style={{ width: "auto", display: "inline" }}>{value}</Typography>
    </div>
}

function SectionHeader(props: any) {
    return (
        <div style={{ marginTop: 12, marginBottom: 5 }}>
            <Typography variant="h6">{props.children}</Typography>
            <Divider />
        </div>);
}